<template>
    <div class="reg_container">
        <my-header></my-header>
        <header>
            <img src="img/index/logo.png" alt="logo">
        </header>
        <form action="">
            <div class="register-box">
                <label for="username" class="username_label">  
                    <input id="i1" maxlength="20" type="text" placeholder="" v-model="uname">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_label">   
                    <input id="i2" maxlength="20" type="password" placeholder="" v-model="password">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_label">   
                    <input id="i3" maxlength="20" type="password" placeholder="">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_babel pr-2">  0086
                    <input id="i4" class="phone" maxlength="20" type="text" placeholder="" v-model="tel">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_label">  
                    <input id="i5" maxlength="20" type="text" placeholder="">
                </label>
                <span id="code"></span>
                <div class="tips"></div>
            </div>
            <div class="arguement">
                <input type="checkbox" id="xieyi">
                    <a href="javascript:;"></a>
                    <a href="javascript:;"></a>
                <div class="tips"></div>
            </div>
            <div class="submit_btn">
                <button type="submit" id="submit_btn" @click="submit_btn">   </button>
            </div>
        </form>
        <my-footer></my-footer>
    </div>
</template>
<script>
import vue from 'vue'
import myHeader from '@/components/header.vue'
import myFooter from '@/components/footer.vue'
export default {
    data(){
        return {
            uname:"",
            password:"",
            tel:"",
        }
    },
    methods:{
        submit_btn(e){
            var uname=this.uname;
            var password=this.password;
            var tel=this.tel;
            //
            var preg=/^[0-9A-Za-z]{3,10}$/;
            if(!preg.test(uname)){
                alert("");
                return;
            }
            //
            var wreg=/^\d{3,6}$/;
            if(!wreg.test(password)){
                alert("");
                return;
            }
            //
            var treg=/^1[3-8]\d{9}$/;
            if(!treg.test(tel)){
                alert("");
                return;
            }
            //
            if(!$("#xieyi")[0].checked){						
                $("#xieyi").next().next().next(".tips").text("");
                $("#xieyi").next().next().next(".tips").css("color",'red');
                e.preventDefault();
                return;
            }
            //ajax
            var url="reg?username="+uname+"&password="+password+"&tel="+tel;
            this.axios.get(url).then(res=>{
                if(res.data.code==1){
                    alert(res.data.msg);
                    this.$router.push("/index");
                }
            })
        }
    },
    components:{
        myFooter,
        myHeader,
    }
}
</script>
<style scoped>
    /**/
