    background: url("/img/reg/reg_bj.jpg") no-repeat;
    background-size: 100% 100%;
}
/*header*/
header>img{
    position: absolute;
    left: 20%;
    top:8%;
}
/**/
/**/
form{
    position: relative;
    left: 70%;
	width: 400px;
    padding-top: 100px;
    padding-bottom: 50px;
}
.register-box{
	border: 1px solid #ddd;
    margin-bottom: 32px;
}
.register-box label{
	height: 52px;
    line-height: 52px;
    display: inline-block;
}
.register-box input{
	width: 190px;
    height: 19px;
    padding-bottom: 11px;
    padding-left: 20px;
    padding-top: 16px;
    font-size: 14px;
    color: #666;
    background: transparent;
    font-family: "Microsoft YaHei","Hiragino Sans GB";
}
