/*  enter*/
$(function(){
    $("#username").focus();
    $(document).keyup(function(event){
        if(event.keyCode==13){
            $("#login_btn").click();
        }
    });
});
/**/
$(function(){
	//input
	$('#i1').focus(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text(",3-10");
		}
	})
	$('#i2').focus(function(){
		if($(this).val().length==0){
		    $(this).parent().next("div").text("3-6");
		}
	})
	$('#i3').focus(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
		}
    })
    $('#i4').focus(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
		}
	})
	$('#i5').focus(function(){	
		if($(this).val().length==0){
			$(this).parent().next().next("div").text("");
		}
	})
	//input
	//
	$('#i1').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if($(this).val().length>=3&& !isNaN($(this).val())){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'red');
		}else{
            $(this).parent().next("div").text("");
        }
	})
	//
	$('#i2').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if($(this).val().length<3 && $(this).val().length>6){
			$(this).parent().next("div").text("3-6");
			$(this).parent().next("div").css("color",'red');
		}else{
			$(this).parent().next("div").text("");
		}		
	})
//	
	$('#i3').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if($(this).val()!=$('#i2').val()){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'red');
		}else{
			$(this).parent().next("div").text("");
		}		
	})
//	
	$('#i4').blur(function(){
        var preg=/^1[3-8]\d{9}$/;
        var p=$(this).val();
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if(!preg.test(p)){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'red');
		}else{
			$(this).parent().next("div").text("");
		}		
	})
// 	
//	 
	function code(){
		var str="qwertyuiopasdfghjklzxcvbnm1234567890QWERTYUIOPLKJHGFDSAZXCVBNM";
		var str1=0;
		for(var i=0; i<4;i++){
			str1+=str.charAt(Math.floor(Math.random()*62)) 
		}
		str1=str1.substring(1)
		$("#code").text(str1);
	}
	code();
	$("#code").click(code);	
//	
	$('#i5').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next().next("div").text("");
			$(this).parent().next().next("div").css("color",'#000');
		}else if($(this).val().toUpperCase()!=$("#code").text().toUpperCase()){
			$(this).parent().next().next("div").text("");
			$(this).parent().next().next("div").css("color",'red');
		}else{
			$(this).parent().next().next("div").text("");
		}		
	})
})
